footer {
    background-color: #2d2e32;
    padding: 5rem 0;
    width: 100%;
}

footer h3 {
    font-size: 1.7rem;
    color: white;
}

.footerc {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footerc__socials {
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
}

.footerc__socials a {
    font-size: 2.3rem;
    color: white;
}

.footerc__socials a:hover {
    transform: scale(1.2);
    transition: all 0.2s ease-in-out;
}

/* Media Queries */

@media (max-width: 550px) {
    .footerc {
        flex-wrap: wrap;
        justify-content: center;
        gap: 2rem;
        text-align: center;
    }
}