nav {
    font-size: 1.7rem;
    width: 100%;
    height: auto;
    padding: 25px 40px 25px 50px;
    display: flex;
    justify-content: space-between;
    position: fixed;
    background-color: white;
    top: 0;
    left: 0;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.09);
    z-index: 200;
    align-items: center;
}

nav ul {
    display: flex;
    list-style: none;
    gap: 2rem;
}

nav ul a {
    color: #2d2e32;
    text-decoration: none;
    transition: all 0.23s;
    font-weight: 600;
}

nav ul a:hover {
    color: #147efb;
}

.mobile-menu {
    font-size: 2.3rem;
    cursor: pointer;
    display: none;
}

.mobile-menu:hover {
    color: #147efb;
    transition: all 0.3s;
}

.closed-menu {
    left: -100% !important;
}

.open-menu {
    left: 0 !important;
}

.mobile-nav {
    display: flex;
    width: 100%;
    height: 100vh;
    z-index: 300;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    text-align: center;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
}

.mobile-nav span {
    font-size: 3.3rem;
    position: absolute;
    top: 2rem;
    right: 4rem;
    cursor: pointer;
}

.mobile-nav span:hover {
    color: #147efb;
    transition: all 0.3s;
}

.mobile-nav ul {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    font-size: 2.3rem;
    list-style: none;
}

.mobile-nav ul a {
    color: black;
    text-decoration: none;
    font-weight: 500;
}

.mobile-nav ul a:hover {
    color: #147efb;
    transition: all 0.3s;
}

/* Media Queries */

@media (max-width: 900px) {
    .mobile-menu {
        display: flex;
    }
}

@media (max-width: 400px) {
    nav {
        padding: 25px 30px 25px 30px;
    }
}

@media (max-width: 900px) {
    nav ul a {
        display: none;
    }
}